import { useRecoilValue, useSetRecoilState } from "recoil";
import styled from "styled-components";
import { categoryListState, IToDo, toDoState } from "../atoms";

const ToDoItem = styled.li`
  display: flex;
  border: 1 solid black;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.9);
  border-radius: 15px;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  font-size: 18px;
  margin-top: 10px;
  color: #fefefe;
  height: 50px;
`;

const CategoryButton = styled.button`
  border: none;
  border-radius: 5px;
  background-color: #ff6a3d;
  color: #fefefe;
  padding: 2px 5px;
  margin-left: 5px;
`;

export function ToDo({ id, text, category }: IToDo) {
  const setToDo = useSetRecoilState(toDoState);
  const categoryList = useRecoilValue(categoryListState);

  const handleOnClick = (category: string) => {
    setToDo((currentToDoList) => {
      const targetIndex = currentToDoList.findIndex((todo) => todo.id === id);

      const oldToDo = currentToDoList[targetIndex];
      const newToDo = { ...oldToDo, category } as IToDo;

      return [
        ...currentToDoList.slice(0, targetIndex),
        newToDo,
        ...currentToDoList.slice(targetIndex + 1),
      ];
    });
  };

  const handleOnClickDelete = () => {
    setToDo((currentToDoList) => {
      const targetIndex = currentToDoList.findIndex((todo) => todo.id === id);

      return [
        ...currentToDoList.slice(0, targetIndex),
        ...currentToDoList.slice(targetIndex + 1),
      ];
    });
  };

  return (
    <ToDoItem>
      {text}

      <div>
        {categoryList.map(({ id, text }) => {
          if (category === id) return "";

          return (
            <CategoryButton key={id} onClick={() => handleOnClick(id)}>
              {text}
            </CategoryButton>
          );
        })}
        <CategoryButton onClick={() => handleOnClickDelete()}>
          Delete
        </CategoryButton>
      </div>
    </ToDoItem>
  );
}
