import { atom, selector } from "recoil";

export enum Category {
  "TO_DO" = "TO_DO",
  "DOING" = "DOING",
  "DONE" = "DONE",
}

export interface ICategory {
  id: string;
  text: string;
}

const DefaultCategoryList: ICategory[] = [
  { id: "TO_DO", text: "TO DO" },
  { id: "DOING", text: "Doing" },
  { id: "DONE", text: "Done" },
];

export interface IToDo {
  id: number;
  text: string;
  category: string;
}

export const toDoState = atom<IToDo[]>({
  key: "toDo",
  default: [],
});

export const categoryListState = atom<ICategory[]>({
  key: "categoryList",
  default: DefaultCategoryList,
});

export const categoryState = atom<string>({
  key: "category",
  default: "TO_DO",
});

export const toDoSelector = selector({
  key: "toDoSelector",
  get: ({ get }) => {
    const toDoList = get(toDoState);
    const selectedCategory = get(categoryState);

    return toDoList.filter(({ category }) => category === selectedCategory);
  },
});
