import { useRecoilState, useRecoilValue } from "recoil";
import styled from "styled-components";
import { categoryListState, categoryState, toDoSelector } from "../atoms";
import CreateCategory from "./CreateCategory";
import { CreateToDo } from "./CreateToDo";
import { ToDo } from "./ToDo";

const Container = styled.div`
  max-width: 500px;
  margin: 0 auto;
`;

const Title = styled.h1`
  font-size: 48px;
  display: block;
  text-align: center;
  font-weight: bold;
  padding-block: 30px;
`;

const CategoryTitle = styled.h2`
  font-size: 36px;
  display: block;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 10px;
`;

const Header = styled.div`
  display: flex;
`;

export function ToDoList() {
  // const toDoList = useRecoilValue(toDoState);

  const toDoList = useRecoilValue(toDoSelector);
  const [category, setCategory] = useRecoilState(categoryState);

  const categoryList = useRecoilValue(categoryListState);

  return (
    <Container>
      <Title>To Do</Title>

      <Header>
        <select
          onInput={(e) => {
            setCategory(e.currentTarget.value || "TO_DO");
          }}
        >
          {categoryList.map(({ id, text }) => {
            return (
              <option key={id} value={id}>
                {text}
              </option>
            );
          })}
        </select>
        <CreateCategory />
      </Header>
      <hr />

      <CategoryTitle>
        {categoryList.find(({ id }) => id === category)?.text || "TO DO"}
      </CategoryTitle>

      <CreateToDo />
      <ul>
        {toDoList?.map((item) => (
          <ToDo key={item.id} {...item} />
        ))}
        {/* {toDoList?.length < 1 && "Empty"} */}
      </ul>
    </Container>
  );
}
